import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { TIPage } from '../ti/ti';
import { SIPage } from '../si/si';
import { TKPage } from '../tk/tk';
import { KAPage } from '../ka/ka';
import { MIPage } from '../mi/mi';

@Component({
    selector: 'page-jurusan',
    templateUrl: 'jurusan.html'
})
export class JurusanPage {

    constructor(public navCtrl: NavController) {

    }
    onTI(){
    	this.navCtrl.push(TIPage)
    }

    onSI(){
        this.navCtrl.push(SIPage)
    }

    onTK(){
        this.navCtrl.push(TKPage)
    }

    onKA(){
        this.navCtrl.push(KAPage)
    }
    onMI(){
        this.navCtrl.push(MIPage)
    }
}


